body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root{
  height:100vh;
}

.create-question-container{

  display: inline-block;
  height:100%;
  width:100%;
  border-radius: 10px;
  box-shadow: 0 1px 15px rgba(0,0,0,0.12), 0 1px 16px rgba(0,0,0,0.24);
  background: white;
}
.create-sub-question-item-container {
  min-height:50px;
}
.create-question-item-container-box{
  display: inline-block;
  min-height:115px;
  width:100%;
  border-radius: 5px;
  box-shadow: 0 1px 5px rgba(0,0,0,0.12), 0 1px 6px rgba(0,0,0,0.24);
  background: white;
  margin-bottom:10px;
  padding:10px 20px 10px 10px;
}
.height-full{
  height:100%;
}
.padding-30{
  padding:17px;
}
.padding-40{
  padding:30px;
}
.add-question-button{
  width:45px;

}
.create-matrix-table{
  border-color: #000000 !important;
}
.create-matrix-table:active,
.create-matrix-table:focus,
.create-matrix-table:hover{
  border-color: #000000 !important;
}
.create-sub-question-input{
  border-width:0px 0px 0px 0px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-color: white !important;
}
.create-sub-question-input:active,
.create-sub-question-input:focus,
.create-sub-question-input:hover{
  border-width:0px 0px 0px 0px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-width: 0px !important !important;
  border-right-color: white !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
}
.create-question-input{
  border-width:0px 0px 0px 2px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-color: white !important;
}
.create-question-input:active,
.create-question-input:focus,
.create-question-input:hover{
  border-width:0px 0px 0px 2px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-width: 0px !important !important;
  border-right-color: white !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
}
.create-preview-input{
  border-width:0px 0px 2px 0px!important;
  border-radius: 0px !important;
  border-color: #FFFFFF !important;
  border-right-width: 0px !important !important;
  border-right-color: white !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
  background:  #13C64F !important;
  color:#FFFFFF !important;

}
.create-preview-input:active,
.create-preview-input:focus,
.create-preview-input:hover{
  border-width:0px 0px 2px 0px!important;
  border-radius: 0px !important;
  border-color: #FFFFFF !important;
  border-right-width: 0px !important !important;
  border-right-color: transparent !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
}
.create-preview-text-max{
  width:100%;
  text-align: right;
  color:#FFFFFF;
}
.second-row-container{

}
.create-preview-input-date > div  >.ant-calendar-picker-input {
  background: #13C64F !important;
  color:#FFFFFF !important;
  border-width:0px 0px 2px 0px!important;
  border-color: #FFFFFF !important;
}
.create-preview-input-date > div  > ::-webkit-input-placeholder {
  color: #FFFFFF;
  opacity: 1; /* Firefox */
}
.create-preview-input-date > div  > ::-ms-input-placeholder {
  color: #FFFFFF;
  opacity: 1; /* Firefox */
}
.create-preview-input-date > div  > ::placeholder {
  color: #FFFFFF;
  opacity: 1; /* Firefox */
}
.create-preview-input-date > div  >  .ant-calendar-picker-icon {
  color: #FFFFFF;
  opacity: 1; /* Firefox */
}
.create-preview-input-date > div  >  .ant-calendar-picker-clear {
  background: #13C64F !important;
}
.create-preview-drag-container > p{
  color:#ffffff ;
}
.create-preview-drag-container > .ant-upload.ant-upload-drag {
  border: 1px solid #ffffff !important;
}
.preview-upload-text {
  color:#ffffff !important;
}

.create-question-input-text-answer{

  border-width:0px 0px 0px 0px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-color: white !important;

}
.create-question-input-text-answer:active,
.create-question-input-text-answer:focus,
.create-question-input-text-answer:hover{
  border-width:0px 0px 0px 0px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-width: 0px !important !important;
  border-right-color: white !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
}
.create-preview-description{
  color:#FFFFFF;
  text-align: center;
}
.ant-upload.ant-upload-select{
  display: block !important;
}
.badge-container{
  margin-top: 5px;
}

.preview-header{
  height:7%;
  border-top-right-radius:5px;
  border-top-left-radius:5px;

}
.preview-body{
  height:93%;
  border-bottom-right-radius:5px;
  border-bottom-left-radius:5px;

}
.ant-select-arrow{
  color:white !important;
}
.ant-select-disabled .ant-select-selection{
  background: white !important;
  color:black !important;
}
.ant-select-selection__rendered{
  line-height: 1 !important;
  margin-top:7px ;

}
.ant-select-selection--single{
    height:35px !important;
}
.ant-carousel .slick-slide {
  text-align: center;
  height: 160px;
  line-height: 160px;
  background: #364d79;
  overflow: hidden;
}

.ant-carousel .slick-slide h3 {
  color: #fff;
}
.ant-carousel .slick-slide img{
  display: inline-block !important;
  text-align: center !important;
}
.margin-top-30{
  margin-top:30px;
}
.margin-top-15{
  margin-top:15px;
}
.new-subgroup{
  margin-left: 30px;
  padding:5px;
}
.new-subgroup-box{
  padding:5px 5px;
  background: #13C64F ;
  color:white;
  text-align: center;
  border-radius: 5px;
  cursor:pointer;
}

.signup-wrapper{
    background: url(/static/media/signup-bg.0393d9fe.png) no-repeat center center;
    background-size: cover;
}
.img-responsive{
  content:url(data:image/png;base64,);
}
.signup-row img{
    display: inline-block;
    text-align: center;
    margin-top: 60px
}
.signup-row{
    text-align: center;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
}
.signup-headline{
    font-size: 36px;
    font-family: 'Roboto',sans-serif;
    line-height: 40px;
    color: #000000;
    margin: 40px 0;
    font-weight: 600;
}
.signup-form .form-group{
    margin-bottom: 28px;
}
.signup-form .form-group.sf-first-group{
    margin-right: 15px;
}
.signup-form input.form-control.sf-first{
    background: url(data:image/png;base64,) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-email{
    background: url(data:image/png;base64,) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-company{
    background: url(data:image/png;base64,) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-password{
    background: url(data:image/png;base64,) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-mobile{
    background: url(data:image/png;base64,) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-address{
    background: url(data:image/png;base64,) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control{
    border: none;
    background-color: #f4f4f4;
    box-shadow: none;
    color: #000;
    padding-left: 65px;
    font-size: 18px;
    border-radius: 10px;
    font-family: 'Roboto',sans-serif;
    font-weight: 300;
    height: 75px;
}
.signup-form input.form-control.sf-none{
    padding-left: 30px;
}
.btn.btn-fp-signup{
    width: 100%;
    background-color: #1AC551;
    height: 66px;
    border: none;
    box-shadow: none;
    font-size: 24px;
    color: #ffffff;
    text-transform: uppercase;
    font-family: 'Roboto',sans-serif;
    margin-top: 30px;
    max-width: 255px;
    font-weight: 500;
    border-radius: 10px;

}
.already-desc a.login-here-link{
    color: #13c64f;
    font-style: normal;
}
.already-desc{
    font-size: 18px;
    font-style: italic;
    color: #3a4d5c;
    font-weight: 300;
    font-family: 'Roboto',sans-serif;
    width: 100%;
    float: left;
}
.signup-agreement-sec{
    width: 100%;
    float: left;
}
.by-signing-desc{
    font-size: 14px;
    color: #3a4d5c;
    margin-bottom: 10px;
    font-weight: 300;
    font-family: 'Roboto',sans-serif;
    width: 100%;
    float: left;
}
form.signup-form{
    margin-bottom: 35px;
    padding-bottom: 35px;
}
.btn-file {
    position: relative;
    overflow: hidden;
}
.btn-file input[type=file] {
    position: absolute;
    top: 0;
    right: 0;
    min-width: 100%;
    min-height: 100%;
    font-size: 100px;
    text-align: right;
    filter: alpha(opacity=0);
    opacity: 0;
    outline: none;
    background: white;
    cursor: inherit;
    display: block;
}
.input-group-btn:last-child>.btn{
    z-index: 2;
    margin-left: -1px;
    width: 100%;
    height: 72px;
    line-height: 54px;
    background-color: #fff;
    border: 3px solid #13c64f;
    border-radius: 10px;
    color: #13c64f;
    font-size: 22px;
    font-family: 'Roboto',sans-serif;
    font-weight: 500;
}
/*footer css*/
.fp-main-footer-section{
    background-color: #f4f4f4;
    float: left;
    width: 100%;
    padding: 40px 0;
    border-top: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
}
.footer-link-head {
    font-size: 16px;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 10px;
}
.fp-foot-links ul li{
    list-style-type: none;
}
.fp-foot-links ul li a{
    font-size: 14px;
    font-weight: 300;
    font-family: 'Roboto', sans-serif;
    color: #000000;
    line-height: 30px;
}
.footer-copy-rights{
    background-color: #f4f4f4;
    float: left;
    width: 100%;
    padding: 20px 0;
}
.row.copy-right-desc{
    padding: 0 15px;
}
/*footer css*/

