.signup-wrapper{
    background: url(../Assets/Images/signup-bg.png) no-repeat center center;
    background-size: cover;
}
.img-responsive{
  content:url(../Assets/Images/signup-logo.png);
}
.signup-row img{
    display: inline-block;
    text-align: center;
    margin-top: 60px
}
.signup-row{
    text-align: center;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
}
.signup-headline{
    font-size: 36px;
    font-family: 'Roboto',sans-serif;
    line-height: 40px;
    color: #000000;
    margin: 40px 0;
    font-weight: 600;
}
.signup-form .form-group{
    margin-bottom: 28px;
}
.signup-form .form-group.sf-first-group{
    margin-right: 15px;
}
.signup-form input.form-control.sf-first{
    background: url(../Assets/Images/firstname-icon.png) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-email{
    background: url(../Assets/Images/email-icon.png) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-company{
    background: url(../Assets/Images/company-icon.png) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-password{
    background: url(../Assets/Images/password-icon.png) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-mobile{
    background: url(../Assets/Images/mobile-icon.png) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control.sf-address{
    background: url(../Assets/Images/address-icon.png) no-repeat 16px #f4f4f4;
}
.signup-form input.form-control{
    border: none;
    background-color: #f4f4f4;
    box-shadow: none;
    color: #000;
    padding-left: 65px;
    font-size: 18px;
    border-radius: 10px;
    font-family: 'Roboto',sans-serif;
    font-weight: 300;
    height: 75px;
}
.signup-form input.form-control.sf-none{
    padding-left: 30px;
}
.btn.btn-fp-signup{
    width: 100%;
    background-color: #1AC551;
    height: 66px;
    border: none;
    box-shadow: none;
    font-size: 24px;
    color: #ffffff;
    text-transform: uppercase;
    font-family: 'Roboto',sans-serif;
    margin-top: 30px;
    max-width: 255px;
    font-weight: 500;
    border-radius: 10px;

}
.already-desc a.login-here-link{
    color: #13c64f;
    font-style: normal;
}
.already-desc{
    font-size: 18px;
    font-style: italic;
    color: #3a4d5c;
    font-weight: 300;
    font-family: 'Roboto',sans-serif;
    width: 100%;
    float: left;
}
.signup-agreement-sec{
    width: 100%;
    float: left;
}
.by-signing-desc{
    font-size: 14px;
    color: #3a4d5c;
    margin-bottom: 10px;
    font-weight: 300;
    font-family: 'Roboto',sans-serif;
    width: 100%;
    float: left;
}
form.signup-form{
    margin-bottom: 35px;
    padding-bottom: 35px;
}
.btn-file {
    position: relative;
    overflow: hidden;
}
.btn-file input[type=file] {
    position: absolute;
    top: 0;
    right: 0;
    min-width: 100%;
    min-height: 100%;
    font-size: 100px;
    text-align: right;
    filter: alpha(opacity=0);
    opacity: 0;
    outline: none;
    background: white;
    cursor: inherit;
    display: block;
}
.input-group-btn:last-child>.btn{
    z-index: 2;
    margin-left: -1px;
    width: 100%;
    height: 72px;
    line-height: 54px;
    background-color: #fff;
    border: 3px solid #13c64f;
    border-radius: 10px;
    color: #13c64f;
    font-size: 22px;
    font-family: 'Roboto',sans-serif;
    font-weight: 500;
}
/*footer css*/
.fp-main-footer-section{
    background-color: #f4f4f4;
    float: left;
    width: 100%;
    padding: 40px 0;
    border-top: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
}
.footer-link-head {
    font-size: 16px;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 10px;
}
.fp-foot-links ul li{
    list-style-type: none;
}
.fp-foot-links ul li a{
    font-size: 14px;
    font-weight: 300;
    font-family: 'Roboto', sans-serif;
    color: #000000;
    line-height: 30px;
}
.footer-copy-rights{
    background-color: #f4f4f4;
    float: left;
    width: 100%;
    padding: 20px 0;
}
.row.copy-right-desc{
    padding: 0 15px;
}
/*footer css*/
